import React from 'react';
import PropTypes from 'prop-types';
import './Home.css';
import { Link } from 'react-router-dom';

function Getimage({key, name, year, author, path, rating}){
  return (
    <div className="image">
        <Link to ={{
            pathname: '/gallery-detail',
            state:{name, year, author, path, rating},
        }}>
      <img src = {path} alt = {name} title = {name}/>
      <h1 className="image__title">작품명 : {name} </h1>
      <span className="image__year">작성일 : {year} </span>
      <p className="image__author">만든이 : {author.slice(0,90)} </p>
      <p className="image__rating">평점 : {rating} / 5.0</p>
      </Link>
    </div>
  );
}
